


import { ChangeDetectorRef, Component, Input, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { VhAlgorithm, VhAuth, VhQuerySales } from 'ionic-vhframeworks';
import { VhComponent } from 'src/app/sales/components/vh-component/vh-component';
import { LanguageService } from 'src/app/sales/services/language.service';
import { FunctionService } from 'src/app/sales/services/function.service';
import { VhReturnCustomerInvoiceDetail, VhReturnCustomerInvoiceEdit } from '../bill-type3-interface';
import { CustomerReturnedA4Component } from 'src/app/sales/components/print/customer-returned/customer-returned-a4/customer-returned-a4.component';
import { CustomerReturnedA5Component } from 'src/app/sales/components/print/customer-returned/customer-returned-a5/customer-returned-a5.component';
import { CustomerReturnedK80Col4Component } from 'src/app/sales/components/print/customer-returned/customer-returned-k80-col4/customer-returned-k80-col4.component';
import { CustomerReturnedK80Col3Component } from 'src/app/sales/components/print/customer-returned/customer-returned-k80-col3/customer-returned-k80-col3.component';
import { CustomerReturnedK57Col3Component } from 'src/app/sales/components/print/customer-returned/customer-returned-k57-col3/customer-returned-k57-col3.component';

@Component({
  selector: 'bill-type3-detail',
  templateUrl: './detail.component.html',
  styleUrls: ['./detail.component.scss']
})
export class DetailComponent {
  tableHeight: string;
  dataRestore: any = this.router.getCurrentNavigation().extras.state.dataRestore
  @Input() url = '';
  public invoiceInfo: any;
  public invoice: VhReturnCustomerInvoiceEdit = null;
  invoice_detail: VhReturnCustomerInvoiceDetail[]
  subTotal = 0;
  constructor(
    private router: Router,
    private vhQuerySales: VhQuerySales,
    private lang: LanguageService,
    private vhAuth: VhAuth,
    private vhComponent: VhComponent,
    public vhAlgorithm: VhAlgorithm,
    private cdRef: ChangeDetectorRef,
    private fncService: FunctionService
  ) {
    this.getDataInvoice(this.router.getCurrentNavigation().extras.state.id)


  }

  ngOnInit(): void {
    //Called after the constructor, initializing input properties, and the first call to ngOnChanges.
    //Add 'implements OnInit' to the class.
    

  }

  init(invoice: VhReturnCustomerInvoiceEdit = null, invoiceDetail: VhReturnCustomerInvoiceDetail[] = null) {
    

    this.invoice = invoice
    this.invoice_detail = invoiceDetail
    if (invoice && invoiceDetail) {
      this.invoiceInfo = {
        employee: this.vhAuth.getlocalEmployee(this.invoice.id_employee),
        partner: this.vhQuerySales.getlocalCustomer(this.invoice.id_customer),
        paymentName: this.setPaymentName(this.invoice.payment_type)
      }
    } else this.invoiceInfo = { employee: {}, partner: {}, paymentName: '' }
    this.setSubTotal()
  }

  private getDataInvoice(id) {
    this.vhComponent.showLoading("", "transparent-loading").then(() => {
      Promise.all([
        this.vhQuerySales.getBill(id),
        this.vhQuerySales.getBill_details_byId_bill(id)
      ]).then(([invoice, invoiceDetail]: any) => {
        if (!invoiceDetail.length)
          this.init(invoice, [])
        else {
          invoiceDetail = this.vhAlgorithm.sortVietnamesebyASC(invoiceDetail, 'name');
          this.init(invoice, invoiceDetail)
        }
        this.vhComponent.hideLoading(0)
      })
    })
  }
  setSubTotal() {
    let subTotal = 0;
    for (let i of this.invoice_detail) {
      subTotal += (i.price ? i.price * i.quantity : 0)
    }
    this.subTotal = subTotal;
  }
  public setPaymentName(type: 1 | 2 | 3) {
    switch (type) {
      case 1: return this.lang.translate("Cash")
      case 2: return this.lang.translate("Debit")
      case 3: return this.vhQuerySales.getlocalWallet(this.invoice.id_wallet).name
    }
  }
  getUnit(units: any[], ratio) {
    return this.vhQuerySales.getUnit_byRatio(units, ratio).unit
  }

  getLotNumber(lots, _id) {
    let lot = lots.find(item => item._id == _id)
    return lot.lot_number + '\n' + this.fncService.formatDate(lot.date_mfg) + ' - ' + this.fncService.formatDate(lot.date_exp)
  }
  public renderAddress(partner) {
    return (partner.address ? partner.address : '') +
      (partner.district ? ', ' + partner.district : '') +
      (partner.province ? ', ' + partner.province : '') +
      (partner.country ? ', ' + partner.country : '')
  }

  /**
  * ly thng tin d liu trang in
  * @returns Promise<any>
  * @example this.checkPrint()
  */
  checkPrint() {
    return new Promise((resolve, rejects) => {
      let printer = this.vhQuerySales.getLocalPrintPage('page_desktop_customer_returned')
      if (printer && printer.print_sizes[printer.print_sizes.findIndex(e => e._id == printer.print_size_default)].enable) resolve(printer.print_sizes[printer.print_sizes.findIndex(e => e._id == printer.print_size_default)])
      else rejects("No")
    })
  }
  printInvoice() {
    this.checkPrint().then((printer) => {
      if (printer)
        this.vhComponent.showModal(this.renderPrintPage(printer['_id']), { printer: printer, invoice: this.invoice, invoice_detail: this.invoice_detail }, false, false, `modal-print-${printer['_id']}`).then((modal) => {
          modal.onWillDismiss().then(() => this.goBack())
        })
    }, (err) => {
      this.vhComponent.alertMessage(this.lang.translate("Function"), "", this.lang.translate("Please turn on the printer function"), "OK")
        .then(() => this.goBack())
    })
  }


  /**
   * tr v component ca trang in tng ng
   * @example this.renderPrintPage('print_size_a4')
   * @returns component
   */
  private renderPrintPage(type: 'print_size_a4' | 'print_size_a5' | 'print_size_k80_4c' | 'print_size_k80_3c' | 'print_size_k57_3c') {

    switch (type) {
      case 'print_size_k57_3c': return CustomerReturnedK57Col3Component
      case 'print_size_k80_3c': return CustomerReturnedK80Col3Component
      case 'print_size_k80_4c': return CustomerReturnedK80Col4Component
      case 'print_size_a5': return CustomerReturnedA5Component
      case 'print_size_a4': return CustomerReturnedA4Component
      default: return CustomerReturnedA4Component
    }
  }
  goBack() {
    this.router.navigate([this.url], { state: this.dataRestore });
  }
  ngAfterViewChecked() {
    if (document.getElementById('sales-invoice-detail-today') && document.querySelector(".sales-cart-header") && document.querySelector(".ant-table-thead") && document.querySelector(".ant-table-pagination")) {
      this.tableHeight = document.getElementById('sales-invoice-detail-today').clientHeight - document.querySelector(".sales-cart-header").clientHeight - document.querySelector(".ant-table-thead").clientHeight
        - document.querySelector(".ant-table-pagination").clientHeight - 40 + "px";
    }
    this.cdRef.detectChanges();
  }
  goToCart() {
    this.vhQuerySales.getValidityDate(this.invoice.date).then((validitydate: any) => {
      if (validitydate == null) {
        if (this.vhAuth.checkMyPermission("customer_return_enable_edit_bill"))
          this.router.navigate([this.url + '/edit-customer-return'], { state: { dataRestore: this.dataRestore, invoiceDetail: this.invoice_detail, invoice: this.invoice, } })
        else this.vhComponent.showToast(1500, this.lang.translate('You do not have this rights'), "alert-toast")
      }
      else {
        this.vhComponent.alertMessageDesktop("warning", `${this.lang.translate("You can only create or edit the invoice from the date")} ${this.fncService.formatDate(validitydate)}`, 5000)
      }
    })

  }
  deleteInvoice() {
    this.vhQuerySales.getValidityDate(this.invoice.date).then((validitydate: any) => {
      if (validitydate == null) {
        if (this.vhAuth.checkMyPermission("return_enable_cancel_customer")) {
          this.vhComponent.alertConfirm(this.lang.translate("Confirm"), "", `${this.lang.translate("Are you sure to cancel the purchase invoice")}?`, "OK", this.lang.translate("Cancel"))
            .then(() => {
              this.vhComponent.showLoading("", "transparent-loading").then(() => {
                this.dataRestore.invoice = this.dataRestore.invoice.filter(e => e._id != this.invoice._id)
                this.vhQuerySales.deleteBill_Billdetail(this.invoice._id).then(() => {
                  this.vhComponent.hideLoading(0).then(() => {
                    this.vhComponent.showToast(2000,
                      `${this.lang.translate("Customer returned invoice")} ${this.invoice.bill_code} ${this.lang.translate("has been cancelled successfully")}`, "success-toast")
                    this.goBack();
                  })
                })
              })
            }, () => { })
        }
        else this.vhComponent.showToast(1500, this.lang.translate('You do not have this rights'), "alert-toast")
      }
      else {
        this.vhComponent.alertMessageDesktop("warning", `${this.lang.translate("You can only create or edit the invoice from the date")} ${this.fncService.formatDate(validitydate)}`, 5000)
      }
    })


  }
}
